*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
}

ul[class],
ol[class] {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

input,
button,
textarea,
select {
  font: inherit;
}

body {
  font-family: sans-serif;
  background-color: #fff;
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-size: 1.6rem;
}

img {
  max-width: 100%;
  display: block;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  background-color: hsl(0, 0%, 94%);
  font-family: "Poppins", sans-serif;
  display: grid;
  place-items: center;
}

.colored {
  color: hsl(259, 100%, 65%);
}

.main-box {
  background-color: hsl(0, 0%, 100%);
  width: 90%;
  max-width: 60em;
  border-radius: 1em 1em 15em 1em;
}
.main-box .box-head {
  padding: clamp(1.5rem, 4rem, 60px);
  padding-bottom: 0.5em;
  display: flex;
  gap: 1.65em;
}
.main-box .box-head label {
  color: hsl(0, 1%, 44%);
  text-transform: uppercase;
  letter-spacing: 0.2em;
  font-size: 1.5rem;
}
.main-box .box-head input {
  margin-top: 0.25em;
  font-size: 2em;
  padding: 1em;
  width: 6em;
  border-radius: 0.5em;
  border: 2px solid hsl(0, 0%, 94%);
  outline: none;
  transition: border 250ms ease;
}
.main-box .box-head input::-webkit-outer-spin-button,
.main-box .box-head input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.main-box .box-head input:hover {
  cursor: pointer;
}
.main-box .box-head input:hover,
.main-box .box-head input:focus {
  border: 2px solid hsl(259, 100%, 65%);
}
.main-box p {
  padding-left: clamp(1.5rem, 4rem, 60px);
  color: hsl(0, 100%, 67%);
}
.main-box hr {
  margin: clamp(1.5rem, 4rem, 60px);
  width: calc(100% - 2 * clamp(1.5rem, 4rem, 60px));
}
.main-box .division {
  position: relative;
  display: grid;
  place-items: center;
}
.main-box img {
  position: absolute;
  top: 0;
  right: clamp(1.5rem, 4rem, 60px);
  background-color: hsl(0, 0%, 8%);
  padding: 1em;
  border-radius: 50%;
  cursor: pointer;
}
.main-box .calculated {
  font-size: 6rem;
  padding: clamp(1.5rem, 4rem, 60px);
  padding-top: 0;
}

.error {
  font-weight: lighter;
}